<template>
  <div class="content">
    <div class="sidebar">
      <div class="left-nav">
        <ul>
          <li>
            <img src="./images/user.png">
            <a href="#">Аккаунт</a>
          </li>
            <li>
            <img src="./images/settings.png">
            <a href="#">Настройки</a>
          </li>
          <li>
            <img src="./images/info.png">
            <a href="#">Помощь</a>
          </li>
        </ul>
      </div>
    </div>
    <div class="main">
      
      <div v-if="loading" class="profile">
          <div class="loader"></div>
      </div>
      <div v-else class="profile">
        <ul class="photo">
          <li v-if="user.avatar"><img v-bind:src="user.avatar"></li>
          <li v-else><span class="radius">{{ user.name[0] }}</span></li>
          <a href="#" class="changeImage">Изменить</a>
        </ul>
        <ul class="info">
          <li>{{ user.name }} {{ user.lastname }}</li>
          <li>{{ user.birthday }}</li>
          <li>{{ user.phone    }}</li>
          <li>{{ user.gender   }}</li>
        </ul>
      </div>

    </div>
  </div>
</template>

<script>
export default {
  name: 'ProfilePage',
  data() {
    return {
      user: {
        name: '',
        lastname: '',
        birthday: '',
        phone: '',
        gender: '',
        avatar: '',
      },
      token: localStorage.getItem('token'),
      loading: false,
    };
  },
  methods: {
    fetchIt() {
      this.loading = true;
      this.$http.get(`/user/data?access_token=${this.token}`).then((r) => {
        console.log('this photo',  r.data.avatar);
        this.user.name = r.data.name;
        this.user.lastname = r.data.lastname;
        this.user.birthday = r.data.birthday;
        this.user.phone = r.data.phone;
        this.user.gender = r.data.gender;
        this.user.avatar = r.data.avatar;
        this.loading = false;
        this.$emit('setUserNameforHeader', r.data.name);
        }).catch(err => console.log(err.response));
    },
  },
  mounted() {
    this.fetchIt();
  },
  created() {
  },
};
</script>


